import React, { useEffect, useRef, useState } from "react";
import { Room } from "livekit-client"; // ✅ Removed incorrect `connect` import
import Draggable from "react-draggable";
import "./VideoCall.css"; 

const VideoCall = ({ roomUrl, token, onClose }) => { // ✅ Added token as a prop
  const videoContainer = useRef(null);
  const [room, setRoom] = useState(null);

  useEffect(() => {
    const joinRoom = async () => {
      const newRoom = new Room(); // ✅ Create a Room instance

      try {
        await newRoom.connect(roomUrl, token); // ✅ Use correct `connect` function
        setRoom(newRoom);

        // Attach video to the container
        newRoom.localParticipant.videoTracks.forEach((trackPublication) => {
          if (trackPublication.track) {
            trackPublication.track.attach(videoContainer.current);
          }
        });

        console.log("Connected to LiveKit room!");
      } catch (error) {
        console.error("Failed to connect to room:", error);
      }
    };

    joinRoom();

    return () => {
      if (room) {
        room.disconnect(); // ✅ Proper cleanup
        console.log("Disconnected from LiveKit room.");
      }
    };
  }, [roomUrl, token]); // ✅ Add token as a dependency

  return (
    <Draggable>
      <div className="video-popup">
        <div ref={videoContainer}></div>
        <button className="close-btn" onClick={onClose}>Close</button>
      </div>
    </Draggable>
  );
};

export default VideoCall;
